import React, { useContext } from "react"
// import { BuilderStoreContext } from '@builder.io/react';

import { Field } from "@steedos/builder-form"
import _ from "lodash"
import { useQuery } from "react-query"
import { ObjectContext } from "../"
import { observer } from "mobx-react-lite"
import { FormContext } from "antd/es/form/context"
import { FormModel, useMst } from "@steedos/builder-store"

export type ObjectFieldProps = {
  objectApiName?: string
  fieldName: string
  required: boolean
  readonly: boolean
}

export const getFormFieldProps = (
  formFieldProps: any,
  field: any,
  readonly: boolean
) => {
  const fieldType = field.type
  // console.log("field===================",field);

  switch (fieldType) {
    case "datetime":
      formFieldProps.valueType = "dateTime"
      formFieldProps.readonly = readonly
      break

    case "boolean":
      formFieldProps.valueType = "switch"
      formFieldProps.readonly = readonly
      break

    case "number":
      formFieldProps.valueType = "digit"
      formFieldProps.readonly = readonly
      break

    case "currency":
      formFieldProps.valueType = "money"
      formFieldProps.readonly = readonly
      break
    case "autonumber":
      formFieldProps.valueType = "index"
      formFieldProps.readonly = readonly
      break
    case "url":
      formFieldProps.valueType = "href"
      break
    case "lookup":
      // return (
      //   <div>{`${fieldType}`}</div>
      // )
      formFieldProps.valueType = "lookup"
      formFieldProps.readonly = false
      formFieldProps.referenceTo = field.reference_to
      break
    case "master_detail":
      return <div>{`${fieldType}`}</div>
  }
  // console.log('+++++++',formFieldProps);

  return formFieldProps
}

export const ObjectField = observer((props: any) => {
  const store = useMst()
  const objectContext = useContext(ObjectContext)
  const context = useContext(FormContext)
  const formId = context.name ? context.name : "default"
  const { fieldName, required, readonly } = props
  let objectApiName = props.objectApiName
  // console.log("props=========",props);
  // react useif
  const { isLoading, error, data, isFetching } = useQuery(
    objectApiName,
    async () => {
      return await objectContext.requestObject(objectApiName)
    }
  )
  if (!objectApiName || !fieldName) return <div></div>

  const objectSchema: any = data

  if (!objectSchema) return <div>Field Loading...</div>

  //TODO  fields['name', 'type']

  const field: any = _.find(objectSchema.fields, (field, key) => {
    return fieldName === key
  })

  if (!field) {
    return <div>{`${objectApiName}${fieldName}`}</div>
  }

  // 
  const fieldType: string = field.type //objectApiNamefieldNametype
  let objectFieldMode = store.forms[formId].mode
  let formFieldProps: any = {
    name: fieldName,
    mode: objectFieldMode,
    label: field.label,
    placeholder: field.help,
    hidden: field.hidden,
    valueType: fieldType,
    required: field.required,
    options: field.options,
    readonly: field.readonly,
  }

  if (formFieldProps.mode == "edit") {
    if (field.omit) {
      formFieldProps.hidden = true
    }
  } else if (formFieldProps.mode == "read") {
    if (field.omit) {
      formFieldProps.readonly = true
    }
  }

  // let fieldProps = {

  // };
  if (fieldType === "formula") {
    let fieldProps = {
      fieldType: field.data_type,
      readonl: true,
    }
    // formFieldProps = getFormFieldProps(formFieldProps, field.data_type, true);
    formFieldProps = getFormFieldProps(
      formFieldProps,
      Object.assign({}, field, { type: field.data_type }),
      true
    )
  } else if (fieldType === "summary") {
    formFieldProps = getFormFieldProps(formFieldProps, field.summary_type, true)
  } else {
    formFieldProps = getFormFieldProps(
      formFieldProps,
      field,
      formFieldProps.readonly
    )
  }

  if (formFieldProps.required) {
    formFieldProps.rules = [
      {
        required: true,
        message: `${formFieldProps.label}...`,
      },
    ]
  }
  // console.log("formFieldProps===============",formFieldProps);

  // ProFormText
  return <Field {...formFieldProps} />
  // if (fieldType === 'lookup') {
  //   return (
  //     <div></div>
  //     // <ObjectFieldLookup
  //     //   {...rest}
  //     //   label={props.label}
  //     //   name={fieldName}
  //     //   referenceTo={reference_to}
  //     //   enableAdd={true}
  //     //   placeholder={`${props.label}...`}
  //     //   readonly={readonly}
  //     // />
  //   )
  // } else {
  //   return (
  //     <ProField
  //       {...formFieldProps}
  //     />
  //   )
  // }
})
